import React, { useEffect, useState } from "react"

import Layout from "../components/layout"
import SEO from "../components/seo"

import HomeTitle from '../components/homepage/homeTitle'
import {Container, VerticalPadding, Button} from '../styles/globalStyles'
import AniLink from "gatsby-plugin-transition-link/AniLink"
import {transitionColor} from '../globalVariables'
import { navigate } from "gatsby"

const NotFoundPage = () => {

  const [time, setTime] = useState(5);
  
  // function countdown(state) {

  //   if (state.time === 0) {
  //     return null;
  //   }
  //   console.log(state.time);

  //   return {
      
  //     time: state.time - 1,
  //   }
  // }

  var myFunction = function() { 
    navigate('/')    
  };

  var t = 5;

  useEffect(() => {
    setInterval(function() {

      if(t !== -1) {
        // console.log(t)
        setTime(t--)
      }
      
    }, 1000)  
    setTimeout(myFunction, 6000)
  }, [t]);
  


  return(
  <Layout>
      <SEO title="Obrigado" />
      <HomeTitle title="obrigado." leading="@pathfinderdesigns" />
      
      <Container>
        <p>Obrigado pelo seu contacto! Responderemos assim que for possível!</p>
        <p>Será redirecionado para a página principal em {time} segundos.</p>
        
        <VerticalPadding>
        <div style={{marginBottom: '-30px  '}}></div>
        <AniLink 
            cover
            direction='right'                                            
            bg={transitionColor}
            to='/'><Button><button>Página Principal</button></Button>
          </AniLink>
        
          
          
          
        </VerticalPadding>
      </Container>
     
    </Layout>
  )
}

export default NotFoundPage
